<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-job-positions',
  data () {
    return {
      config: {
        title: 'Jabatan',
        model_api: 'job-positions',
        getter: 'job-positions',
        setter: 'job-positions',
        pk_field: 'job_position_name',
        permission: {
          create: 'create-job-positions',
          read: 'view-job-positions',
          update: 'update-job-positions',
          delete: 'delete-job-positions'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'job_position_name', label: 'Nama Jabatan' },
          {
            id: 'job_position_category_id',
            label: 'Kategori Jabatan',
            methods: {
              create: {
                setter: 'master-job-position-categories',
                getter: 'master-job-position-categories',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              update: {
                setter: 'master-job-position-categories',
                getter: 'master-job-position-categories',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              },
              filter: {
                setter: 'master-job-position-categories',
                getter: 'master-job-position-categories',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  }
                }
              }
            }
          },
          {
            id: 'division_id',
            label: 'Divisi',
            methods: {
              create: {
                setter: 'divisions',
                getter: 'divisions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'division_name', code: 'id', display: ['division_name']
                  }
                }
              },
              update: {
                setter: 'divisions',
                getter: 'divisions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'division_name', code: 'id', display: ['division_name']
                  }
                }
              },
              filter: {
                setter: 'divisions',
                getter: 'divisions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'division_name', code: 'id', display: ['division_name']
                  }
                }
              }
            }
          },
          {
            id: 'parent_id',
            label: 'Induk Jabatan',
            methods: {
              create: {
                setter: 'job_positions',
                getter: 'job_positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              update: {
                setter: 'job_positions',
                getter: 'job_positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job_positions',
                getter: 'job_positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'is_structural',
            label: 'Jabatan Struktural?',
            methods: {
              list: { transform: 'is_structural' },
              detail: { transform: 'is_structural' },
              create: {
                type: 'radio',
                value: true,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Jabatan Struktural', code: true }, { label: 'Jabatan non Struktural', code: false }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Jabatan Struktural', code: true }, { label: 'Jabatan non Struktural', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Jabatan Struktural', code: true }, { label: 'Jabatan non Struktural', code: false }]
                  }
                }
              }
            }
          },
          {
            id: 'is_plt',
            label: 'Jabatan PLT?',
            methods: {
              list: { transform: 'is_plt' },
              detail: { transform: 'is_plt' },
              create: {
                type: 'radio',
                value: true,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Jabatan PLT', code: true }, { label: 'Jabatan non PLT', code: false }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Jabatan PLT', code: true }, { label: 'Jabatan non PLT', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Jabatan PLT', code: true }, { label: 'Jabatan non PLT', code: false }]
                  }
                }
              }
            }
          },
          {
            id: 'status_company',
            label: 'Status Perusahaan',
            methods: {
              list: { transform: 'is_kiw' },
              detail: { transform: 'is_kiw' },
              create: {
                type: 'radio',
                value: 'kiw',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'KIW', code: 'kiw' }, { label: 'Eksternal', code: 'external' }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'KIW', code: 'kiw' }, { label: 'Eksternal', code: 'external' }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'KIW', code: 'kiw' }, { label: 'Eksternal', code: 'external' }]
                  }
                }
              }
            }
          },
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          }
        ]
      }
    }
  }
}
</script>



